body {
  margin: 0;
  overflow: hidden;
}

.modal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.close {
  visibility: hidden;
  opacity: 0.2;
  transition-delay: 0s;
}

.modal-bg {
  position: absolute;
  background: transparent;
  width: 100%;
  height: 100%;
}

.modal-container {
  border-radius: 10px;
  max-width: 40vw;
  max-height: 80vh;
  background: #fff;
  position: relative;
  padding: 30px;
  font-size: 2rem;
}

.modal-container h1 {
  font-weight: bold;
  text-align: center;
  margin: 1vw;
}

.modal-container h6 {
  text-align: center;
  margin: 0;
  font-style: italic;
}

.modal-close {
  position: absolute;
  right: 15px;
  top: 15px;
  outline: none;
  appearance: none;
  color: red;
  background: none;
  border: 0px;
  font-weight: bold;
  cursor: pointer;
}

#canvas {
  position: absolute;
  width: 100vh;
  height: 100vh;
}

.yearCounter {
  background-color: white;
  position: absolute;
  z-index: 1;
  top: 2vw;
  left: 93vw;
  width: 5vw;
  height: 5vw;
  font-size: 1vw;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.toggleViewTypeBtn {
  background-color: white;
  position: absolute;
  z-index: 1;
  top: 2vw;
  left: 85vw;
  width: 5vw;
  height: 5vw;
  text-align: center;
  border-radius: 25%;
  border: none;
  font-size: 1vw;
}

.toggleViewTypeBtn:hover {
  cursor: pointer;
}

