@media screen and (max-width: 700px) {
  .yearCounter {
    background-color: white;
    position: absolute;
    z-index: 1;
    top: 90vh;
    left: 80vw;
    width: 8vh;
    height: 8vh;
    font-size: 3vw;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .toggleViewTypeBtn {
    background-color: white;
    position: absolute;
    z-index: 1;
    top: 90vh;
    left: 55vw;
    width: 8vh;
    height: 8vh;
    text-align: center;
    border-radius: 25%;
    border: none;
    font-size: 2vw;
  }

  .modal-container {
    border-radius: 10px;
    max-width: 50vw;
    background: #fff;
    position: relative;
    padding: 30px;
    font-size: 3vw;
  }
}
